import { setConfig, format, FormatError } from 'm-formatter';

const ctx: Worker = self as any;

function stringifyErrors(errs: any[])
{
  return JSON.stringify(errs.map(e => {
    let type: string = "any";
    if(e instanceof FormatError)
      type = "FormatError";
    return {
      ...e,
      type
    }
  }));
}

ctx.addEventListener('message', async (event) => {
  let errs = null;
  let result = null;
  try
  {
    setConfig(event.data.config);
    
    try
    {
      [result, errs] = await format(event.data.code);
    }
    catch(err)
    {
      errs = [err];
    }
  }
  catch(err)
  {
    errs = [err];
  }
  
  errs = errs ?? [];
  
  ctx.postMessage([
    result, 
    stringifyErrors(errs)
  ]);
});